<script>
	import 'animate.css';

export default {
    //
    data() {
		return {
            pageFlag : '',
			tableName: '',
			registerForm: {},
			forgetForm: {},
			rules: {},
			requiredRules: {},
            yonghuxingbieOptions: [],
            paotuixingbieOptions: [],
		}
    },
	mounted() {
		if(this.$route.query.pageFlag=='register'){
			this.tableName = this.$route.query.role;
			if(this.tableName=='yonghu'){
				this.registerForm = {
					yonghuzhanghao: '',
					mima: '',
					mima2: '',
					yonghuxingming: '',
					xingbie: '',
					touxiang: '',
					lianxidianhua: '',
					pquestion: '',
					panswer: '',
					status: '',
				}
			}
			if(this.tableName=='paotui'){
				this.registerForm = {
					paotuizhanghao: '',
					mima: '',
					mima2: '',
					paotuixingming: '',
					touxiang: '',
					xingbie: '',
					lianxidianhua: '',
					pquestion: '',
					panswer: '',
					status: '',
				}
			}
			if ('yonghu' == this.tableName) {
				this.rules.yonghuzhanghao = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.yonghuzhanghao = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yonghu' == this.tableName) {
				this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yonghu' == this.tableName) {
				this.rules.yonghuxingming = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.yonghuxingming = [{ required: true, message: '', trigger: 'blur' }]
			}
			this.yonghuxingbieOptions = ",".split(',');
			if ('yonghu' == this.tableName) {
				this.rules.lianxidianhua = [{ required: true, validator: this.$validate.isMobile, trigger: 'blur' }];
			}
			if(`yonghu` == this.tableName){
				this.rules.pquestion = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.pquestion = [{ required: true, message: '', trigger: 'blur' }]
			}
			if(`yonghu` == this.tableName){
				this.rules.panswer = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.panswer = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yonghu' == this.tableName) {
				this.rules.status = [{ required: true, validator: this.$validate.isIntNumer, trigger: 'blur' }];
			}
			if ('paotui' == this.tableName) {
				this.rules.paotuizhanghao = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.paotuizhanghao = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('paotui' == this.tableName) {
				this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('paotui' == this.tableName) {
				this.rules.paotuixingming = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.paotuixingming = [{ required: true, message: '', trigger: 'blur' }]
			}
			this.paotuixingbieOptions = ",".split(',');
			if ('paotui' == this.tableName) {
				this.rules.lianxidianhua = [{ required: true, validator: this.$validate.isMobile, trigger: 'blur' }];
			}
			if(`paotui` == this.tableName){
				this.rules.pquestion = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.pquestion = [{ required: true, message: '', trigger: 'blur' }]
			}
			if(`paotui` == this.tableName){
				this.rules.panswer = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.panswer = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('paotui' == this.tableName) {
				this.rules.status = [{ required: true, validator: this.$validate.isIntNumer, trigger: 'blur' }];
			}
		}
	},
    created() {
		this.pageFlag = this.$route.query.pageFlag;
    },
    //
    methods: {
		changeRules(name){
			if(this.requiredRules[name]){
				return true
			}
			return false
		},
		// uuid
		getUUID () {
			return new Date().getTime();
		},
        // 
		yonghutouxiangUploadChange(fileUrls) {
			this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
		},
		paotuitouxiangUploadChange(fileUrls) {
			this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
		},

		// 


//
		getSecurity(){
			this.tableName = 'yonghu'
			if(!this.tableName) {
				this.$message.error("");
				return false
			}
			if(!this.forgetForm.username){
				this.$message.error("");
				return false
			}
			this.$http({
				url: this.tableName+`/security?username=` + this.forgetForm.username,
				method: "get",
			}).then(({
				data
			}) => {
				if (data.data && data.code === 0) {
					this.registerForm = data.data;
					this.pageFlag="security2"
				} else {
					if(!data.data) {
						this.$message.error("");
					} else {
						this.$message.error(data.msg);
					}
				}
			});
		},
		//
		validateSecurity(){
			let that = this
			if(this.registerForm.panswer!=this.registerForm.myanswer){
				this.$message.error("");
				return false
			}
			this.$message.success("");
			setTimeout(()=>{
				this.pageFlag="security3";
			},1000)
		},
		// 
		updatePassword() {
			if (!this.registerForm.newpassword) {
				this.$message.error("");
				return;
			}
			if (this.registerForm.newpassword != this.registerForm.repassword) {
				this.$message.error("");
				return;
			}
			this.registerForm.mima = this.registerForm.newpassword;
			this.registerForm.password = this.registerForm.newpassword;
			this.$http.post(this.tableName+`/update`, this.registerForm).then(res => {
			  if (res.data.code === 0) {
				this.$message({
					message: "",
					type: "success",
					duration: 1500,
					onClose: () => {
					  this.$router.push('/login');
					}
				});
			  } else {
				this.$message.error(res.data.msg);
			  }
			});
		},
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					var url=this.tableName+"/register";
					if(`yonghu` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
						this.$message.error(``);
						return
					}
					if(`paotui` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
						this.$message.error(``);
						return
					}
					this.$http.post(url, this.registerForm).then(res => {
						if (res.data.code === 0) {
							this.$message({
								message: '',
								type: 'success',
								duration: 1500,
								onClose: () => {
									this.$router.push('/login');
								}
							});
						} else {
							this.$message.error(res.data.msg);
						}
					});
				} else {
					return false;
				}
			});
		},
		resetForm(formName) {
			this.$refs[formName].resetFields();
		}
    }
}
</script>

