<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="shangpinmingcheng">
				<el-input v-model="ruleForm.shangpinmingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.shangpinmingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.shangpintupian)" prop="shangpintupian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.shangpintupian?ruleForm.shangpintupian:''"
					@change="shangpintupianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="shangpintupian">
				<img v-if="ruleForm.shangpintupian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.shangpintupian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.shangpintupian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="goumaizhanghao">
				<el-input v-model="ruleForm.goumaizhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.goumaizhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="goumaixingming">
				<el-input v-model="ruleForm.goumaixingming" 
					placeholder="" clearable :disabled=" false  ||ro.goumaixingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="lianxidianhua">
				<el-input v-model="ruleForm.lianxidianhua" 
					placeholder="" clearable :disabled=" false  ||ro.lianxidianhua"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shouhuodizhi">
				<el-input v-model="ruleForm.shouhuodizhi" 
					placeholder="" clearable :disabled=" false  ||ro.shouhuodizhi"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="fahuoshijian">
				<el-date-picker
					:disabled=" false  ||ro.fahuoshijian"
					format="yyyy  MM  dd "
					value-format="yyyy-MM-dd"
					v-model="ruleForm.fahuoshijian" 
					type="date"
					placeholder="">
				</el-date-picker> 
			</el-form-item>
			<el-form-item class="add-item" label="" prop="paotuizhanghao">
				<el-select  @change="paotuizhanghaoChange" v-model="ruleForm.paotuizhanghao" placeholder="">
					<el-option
						v-for="(item,index) in paotuizhanghaoOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="paotuixingming">
				<el-input v-model="ruleForm.paotuixingming" 
					placeholder="" clearable :disabled=" false  ||ro.paotuixingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="yonghuzhanghao">
				<el-input v-model="ruleForm.yonghuzhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuzhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="yonghuxingming">
				<el-input v-model="ruleForm.yonghuxingming" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuxingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="fahuozhuangtai">
				<el-select v-model="ruleForm.fahuozhuangtai" placeholder="" :disabled="true  ||ro.fahuozhuangtai" >
					<el-option
						v-for="(item,index) in fahuozhuangtaiOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="fahuoxiangqing">
				<el-input
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.fahuoxiangqing">
					</el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					shangpinmingcheng : false,
					shangpintupian : false,
					goumaizhanghao : false,
					goumaixingming : false,
					lianxidianhua : false,
					shouhuodizhi : false,
					fahuoshijian : false,
					fahuoxiangqing : false,
					paotuizhanghao : false,
					paotuixingming : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
					fahuozhuangtai : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					shangpinmingcheng: '',
					shangpintupian: '',
					goumaizhanghao: '',
					goumaixingming: '',
					lianxidianhua: '',
					shouhuodizhi: '',
					fahuoshijian: '',
					fahuoxiangqing: '',
					paotuizhanghao: '',
					paotuixingming: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
					fahuozhuangtai: '' ,
				},
				paotuizhanghaoOptions: [],
				fahuozhuangtaiOptions: [],


				rules: {
					shangpinmingcheng: [
					],
					shangpintupian: [
					],
					goumaizhanghao: [
					],
					goumaixingming: [
					],
					lianxidianhua: [
					],
					shouhuodizhi: [
					],
					fahuoshijian: [
					],
					fahuoxiangqing: [
					],
					paotuizhanghao: [
					],
					paotuixingming: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
					fahuozhuangtai: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.fahuoshijian = this.getCurDate()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='shangpinmingcheng'){
							this.ruleForm.shangpinmingcheng = obj[o];
							this.ro.shangpinmingcheng = true;
							continue;
						}
						if(o=='shangpintupian'){
							this.ruleForm.shangpintupian = obj[o].split(",")[0];
							this.ro.shangpintupian = true;
							continue;
						}
						if(o=='goumaizhanghao'){
							this.ruleForm.goumaizhanghao = obj[o];
							this.ro.goumaizhanghao = true;
							continue;
						}
						if(o=='goumaixingming'){
							this.ruleForm.goumaixingming = obj[o];
							this.ro.goumaixingming = true;
							continue;
						}
						if(o=='lianxidianhua'){
							this.ruleForm.lianxidianhua = obj[o];
							this.ro.lianxidianhua = true;
							continue;
						}
						if(o=='shouhuodizhi'){
							this.ruleForm.shouhuodizhi = obj[o];
							this.ro.shouhuodizhi = true;
							continue;
						}
						if(o=='fahuoshijian'){
							this.ruleForm.fahuoshijian = obj[o];
							this.ro.fahuoshijian = true;
							continue;
						}
						if(o=='fahuoxiangqing'){
							this.ruleForm.fahuoxiangqing = obj[o];
							this.ro.fahuoxiangqing = true;
							continue;
						}
						if(o=='paotuizhanghao'){
							this.ruleForm.paotuizhanghao = obj[o];
							this.ro.paotuizhanghao = true;
							continue;
						}
						if(o=='paotuixingming'){
							this.ruleForm.paotuixingming = obj[o];
							this.ro.paotuixingming = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
						if(o=='fahuozhuangtai'){
							this.ruleForm.fahuozhuangtai = obj[o];
							this.ro.fahuozhuangtai = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao;
							this.ro.yonghuzhanghao = true;
						}
						if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
							this.ruleForm.yonghuxingming = json.yonghuxingming;
							this.ro.yonghuxingming = true;
						}
					}
				});
				this.$http.get('option/paotui/paotuizhanghao', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.paotuizhanghaoOptions = res.data.data;
					}
				});
				this.fahuozhuangtaiOptions = ",".split(',')

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},
			// 
			paotuizhanghaoChange () {
				this.$http.get('follow/paotui/paotuizhanghao?columnValue=' + this.ruleForm.paotuizhanghao, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						if(res.data.data.paotuixingming){
							this.ruleForm.paotuixingming = res.data.data.paotuixingming
						}
					}
				});
			},

			// 
			// 
			info() {
				this.$http.get(`dingdanxinxi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`dingdanxinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			shangpintupianUploadChange(fileUrls) {
				this.ruleForm.shangpintupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

