class TimeMethod {

	constructor() {}

	//
	addZero(data) {
		if (parseInt(data) < 10) {
			return "0" + String(data);
		}
		return data;
	}

	/**
	 * 
	 */
	getNowTime() {
		const myDate = new Date();
		const year = myDate.getFullYear();
		const mouth = this.addZero(myDate.getMonth() + 1);
		const day = this.addZero(myDate.getDate());
		const hour = this.addZero(myDate.getHours());
		const minute = this.addZero(myDate.getMinutes());
		const second = this.addZero(myDate.getSeconds());
		return year + '-' + mouth + '-' + day + 'T' + hour + ':' + minute + ':' + second
	}

	/**
	 * 
	 * @param {Object} time
	 */
	getTime(time) {
		const myDate = new Date(time);
		const year = myDate.getFullYear();
		const mouth = this.addZero(myDate.getMonth() + 1);
		const day = this.addZero(myDate.getDate());
		const hour = this.addZero(myDate.getHours());
		const minute = this.addZero(myDate.getMinutes());
		const second = this.addZero(myDate.getSeconds());
		return year + '-' + mouth + '-' + day + 'T' + hour + ':' + minute + ':' + second
	}

	/**
	 * @param {Object} timestamp
	 * @param {Object} type
	 * 
	 */
	timestampToTime(timestamp, type) {
		if (String(timestamp).length === 10) {
			//10*1000
			var date = new Date(timestamp * 1000);
		} else {
			var date = new Date(timestamp);
		}
		const Y = date.getFullYear() + '-';
		const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
		const D = date.getDate() + ' ';
		const h = date.getHours() + ':';
		const m = date.getMinutes() + ':';
		const s = date.getSeconds();
		if (type === "date") {
			return Y + M + D;
		} else {
			return Y + M + D + h + m + s;
		}
	}


	/**
	 * @param {Object} time
	 * 
	 */
	timeToTimestamp(time) {
		//000 Date.parse(date); 
		return new Date(time).getTime();
	}


	/**
	 * @param {Object} startTime
	 * @param {Object} endTime
	 * 
	 */
	calculateTime(startTime, endTime) {
		return new Date(startTime) - new Date(endTime)
	}

	/**
	 * @param {Object} time
	 * 
	 */
	getDateToWeek(time) {
		let weekArrayList = [{
				"weekID": 7,
				"weekName": ""
			},
			{
				"weekID": 1,
				"weekName": ""
			},
			{
				"weekID": 2,
				"weekName": ""
			},
			{
				"weekID": 3,
				"weekName": ""
			},
			{
				"weekID": 4,
				"weekName": ""
			},
			{
				"weekID": 5,
				"weekName": ""
			},
			{
				"weekID": 6,
				"weekName": ""
			}
		];
		return weekArrayList[new Date(time).getDay()]
	}

	/**
	 * @param {Object} date
	 *  yyyy-MM-dd HH:mm:ss   yyyy-MM-ddTHH:mm:ss
	 */
	timeFormat(date, type) {
		if (type == "T")
			return date.replace(" ", "T")
		else
			return date.replace("T", " ")
	}

	/**
	 * @param {Object} time
	 * 
	 */
	timeSleep(time) {
		return new Promise((resolve) => setTimeout(resolve, time))
	}


	/**
	 * 
	 * @param dayCount
	 */
	countDateStr(dayCount) {
		let dd = new Date();
		dd.setDate(dd.getDate() + dayCount);
		let ms = dd.getTime();
		return new Date(ms).toJSON();
	}

	/**
	 * @param {Object} time
	 * 0
	 */
	setTimeZero(time) {
		return time.slice(0, 10) + 'T00:00:00.000+00:00';
	}

}


export default new TimeMethod();