<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="ID" prop="uid">
				<el-input v-model="ruleForm.uid" 
					placeholder="ID" clearable :disabled=" false  ||ro.uid"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="ID" prop="fid">
				<el-input v-model="ruleForm.fid" 
					placeholder="ID" clearable :disabled=" false  ||ro.fid"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="name">
				<el-input v-model="ruleForm.name" 
					placeholder="" clearable :disabled=" false  ||ro.name"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.picture)" prop="picture">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.picture?ruleForm.picture:''"
					@change="pictureUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="picture">
				<img v-if="ruleForm.picture.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.picture.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.picture.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="role">
				<el-input v-model="ruleForm.role" 
					placeholder="" clearable :disabled=" false  ||ro.role"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="tablename">
				<el-input v-model="ruleForm.tablename" 
					placeholder="" clearable :disabled=" false  ||ro.tablename"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="alias">
				<el-input v-model="ruleForm.alias" 
					placeholder="" clearable :disabled=" false  ||ro.alias"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="(0:1:2:)" prop="type">
				<el-input v-model.number="ruleForm.type" 
					placeholder="(0:1:2:)" clearable :disabled=" false  ||ro.type"></el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					uid : false,
					fid : false,
					name : false,
					picture : false,
					role : false,
					tablename : false,
					alias : false,
					type : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					uid: '',
					fid: '',
					name: '',
					picture: '',
					role: '',
					tablename: '',
					alias: '',
					type: '',
				},


				rules: {
					uid: [
						{ required: true, message: 'ID', trigger: 'blur' },
					],
					fid: [
						{ required: true, message: 'ID', trigger: 'blur' },
					],
					name: [
						{ required: true, message: '', trigger: 'blur' },
					],
					picture: [
						{ required: true, message: '', trigger: 'blur' },
					],
					role: [
					],
					tablename: [
					],
					alias: [
					],
					type: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='uid'){
							this.ruleForm.uid = obj[o];
							this.ro.uid = true;
							continue;
						}
						if(o=='fid'){
							this.ruleForm.fid = obj[o];
							this.ro.fid = true;
							continue;
						}
						if(o=='name'){
							this.ruleForm.name = obj[o];
							this.ro.name = true;
							continue;
						}
						if(o=='picture'){
							this.ruleForm.picture = obj[o].split(",")[0];
							this.ro.picture = true;
							continue;
						}
						if(o=='role'){
							this.ruleForm.role = obj[o];
							this.ro.role = true;
							continue;
						}
						if(o=='tablename'){
							this.ruleForm.tablename = obj[o];
							this.ro.tablename = true;
							continue;
						}
						if(o=='alias'){
							this.ruleForm.alias = obj[o];
							this.ro.alias = true;
							continue;
						}
						if(o=='type'){
							this.ruleForm.type = obj[o];
							this.ro.type = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`friend/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`friend/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			pictureUploadChange(fileUrls) {
				this.ruleForm.picture = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

